$colorDefault: #002142;
$frameSize: 500px;
$velocityRotateStar: 200;
$starSize: 3px;
$dimension: 0;
$colorMoon: #FFD453;

$orange-light: #d38bfd;
$orange: #452aa6;
$orange-dark: #240530;

$blue-light: #70a27d;
$blue: #4552a1;
$blue-dark: #371a6a;

$bg-light: #ee7752;
$bg-semi: #e73c7e;
$bg-middle: #23a6d5;
$bg-dark: #23d5ab;


#contact_wrap {
  width: 100%;
  height: 100%;
  position: relative;
  font-family: 'NanumSquareRound';
  color: rgba(100, 65, 146, 0.6);


  .contact {
    width: 100%;
    height: 963px;
  }

  .row {
    width: 100%;
    height: 100%;

    .C_top {
      width: 100%;
      height: 15%;
      // background-color: #8686867c;
      position: relative;
      color: #fff;
      display: flex;
      align-items: flex-end;

      h1 {
        margin-top: 50px;
        margin-left: 50px;
        font-size: 40px;
        height: 100px;
        -webkit-font-smoothing: antialiased;
      }

      h1 span {
        position: relative;
        top: 20px;
        display: inline-block;
        animation: bounce .7s ease infinite alternate;
        font-family: 'Black Han Sans', sans-serif;
        font-size: 70px;
        color: #FFF;
        text-shadow: 0 1px 0 #CCC,
          0 2px 0 #CCC,
          0 3px 0 #CCC,
          0 4px 0 #CCC,
          0 5px 0 #CCC,
          0 6px 0 transparent,
          0 7px 0 transparent,
          0 8px 0 transparent,
          0 9px 0 transparent,
          0 10px 10px rgba(0, 0, 0, .4);
      }

      h1 span:nth-child(2) {
        animation-delay: .1s;
      }

      h1 span:nth-child(3) {
        animation-delay: .2s;
      }

      h1 span:nth-child(4) {
        animation-delay: .3s;
      }

      h1 span:nth-child(5) {
        animation-delay: .4s;
      }

      h1 span:nth-child(6) {
        animation-delay: .5s;
      }

      h1 span:nth-child(7) {
        animation-delay: .6s;
      }

      h1 span:nth-child(8) {
        animation-delay: .7s;
      }

      @keyframes bounce {
        100% {
          top: -20px;
          text-shadow: 0 1px 0 #CCC,
            0 2px 0 #CCC,
            0 3px 0 #CCC,
            0 4px 0 #CCC,
            0 5px 0 #CCC,
            0 6px 0 #CCC,
            0 7px 0 #CCC,
            0 8px 0 #CCC,
            0 9px 0 #CCC,
            0 50px 25px rgba(0, 0, 0, .2);
        }
      }

      .planet2 {
        position: absolute;
        top: 2%;
        right: 4%;
        width: 300px;
        height: 300px;
        background-size: 400% 400%;
        animation: daylight 15s ease infinite;
        border-radius: 50%;
        overflow-x: hidden;
      }

      .planet {
        position: absolute;
        top: 50%;
        left: 50%;
        z-index: 2;
        width: 120px;
        height: 120px;
        background: linear-gradient(to bottom, $orange-light 1%, $orange 50%, $orange-dark 100%);
        border-radius: 50%;
        transform: rotate(-15deg) translate(-35%, -55%);
      }

      @supports not (clip-path: inset(50% 0 0 0)) {

        .ring-before,
        .ring-after,
        .ring-bigger-before,
        .ring-bigger-after {
          display: none;
        }
      }

      .ring-before,
      .ring-after,
      .ring-bigger-before,
      .ring-bigger-after {
        content: "";
        width: 126px;
        height: 42px;
        border-radius: 50%;
        border: 3px solid rgba(#e794f1, .8);
        position: absolute;
        top: 125px;
        left: 84px;
        transform: skew(-54deg);
      }

      .ring-bigger-before,
      .ring-bigger-after {
        transform: skew(-54deg) scale(1.3);
        border-color: rgb(104, 120, 211)
      }

      .ring-before,
      .ring-bigger-before {
        z-index: 3;
        clip-path: inset(50% 0 0 0);
      }

      .ring-after,
      .ring-bigger-after {
        z-index: 1;
        clip-path: inset(0 0 50% 0);
      }

      .sub-planet {
        position: absolute;
        top: 24%;
        left: 75%;
        z-index: 4;
        width: 35px;
        height: 35px;
        background: linear-gradient(to bottom, $blue-light 1%, $blue 50%, $blue-dark 100%);
        border-radius: 50%;
        transform: rotate(-15deg);
      }

      .orbit {
        width: 2px;
        height: 2px;
        background: white;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 9;
      }

      @for $i from 1 through 6 {

        .orbit-#{$i} {
          animation: orbit-#{$i} 5s * $i linear infinite;
        }

        @keyframes orbit-#{$i} {
          from {
            transform: rotate(0deg) translateX(50px + 15px * $i) rotate(0deg)
          }

          to {
            transform: rotate(360deg) translateX(50px + 15px * $i) rotate(-360deg)
          }
        }
      }

      .star {
        position: absolute;
        left: 0;
        top: 50px;
        width: 1px;
        height: 1px;
        background-color: white;
        animation: shooting 5s linear infinite;

        &:before {
          content: "";
          position: absolute;
          width: 100px;
          height: 1px;
          right: 1px;
          top: 0;
          background: linear-gradient(to right, rgba(black, 0) 0%, rgba(white, .4) 100%);
        }
      }

      .star-2 {
        top: 100px;
        animation: shooting 8s 1s linear infinite
      }

      .star-3 {
        top: 250px;
        animation: shooting 3s 2s linear infinite
      }

      @keyframes daylight {
        0% {
          background-position: 0% 50%
        }

        50% {
          background-position: 100% 50%
        }

        100% {
          background-position: 0% 50%
        }
      }

      @keyframes shooting {
        from {
          left: -100px
        }

        to {
          left: calc(100% + 100px)
        }
      }

    }

    .C_center {
      width: 100%;
      height: 70%;
      // background-color: #b4b4b462;
      display: flex;

      .Con_l {
        width: 25%;
        height: 100%;
        // background-color: #8a8a8a62;
        background-image: url(../../public/img/sign1.png);
        background-size: 60%;
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0.5;
      }

      .Con_c {
        width: 50%;
        height: 100%;
        // background-color: #c9b6b679;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;

        .swiper_wrap {
          width: 37%;
          height: 72%;
          margin-left: -40px;

          .card_slide {
            width: 100%;
            height: 100%;

            .swiper-wrapper {
              .swiper-slide {
                background-color: aliceblue;
                border-radius: 10px;
                border: 3px solid #7864a8;
                display: flex;
                align-items: center;
                flex-direction: column;
                

                .top_Cc {
                  width: 100%;
                  height: 9%;
                  background-color: #afb5e9;
                  border-bottom: 3px solid #9a8ac1;
                  font-weight: 600;
                  display: flex;
                  justify-content: flex-end;

                  .link_ex {
                    display: flex;
                    justify-content: flex-end;
                    align-items: center;
                    height: 100%;
                    width: 70%;
                    color: #fff;

                    a {
                      color: #fff;
                    }

                    p {
                      margin-left: 50px;
                      margin-right: 8px;
                      font-size: 20px;

                    }
                  }
                }

                .Cc_wrap {
                  width: 92%;
                  height: 85%;
                  margin-top: 10px;
                  background-color: #ebe2f5;
                  display: flex;
                  align-items: center;
                  font-size: 17px;
                  font-weight: 600;
                  font-family: 'D2Coding';
                  color: rgb(53, 35, 77);
                  border-radius: 10px;

                  & img {
                    width: 160px;
                  }

                  .img_c {
                    width: 100%;
                    height: 55%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-direction: column;
                    text-align: center;
                             
                  }

                  .img_c2 {
                    width: 100%;
                    height: 72%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-direction: column;
                    text-align: center;

                  }

                  .img_c3 {
                    width: 100%;
                    height: 55%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-direction: column;
                    text-align: center;
                    & img {
                      width: 180px;
                    }
             
                  }
                  .img_c4 {
                    width: 100%;
                    height: 70%;
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
                    flex-direction: column;
                    text-align: center;
                    font-size: 16px;

                    a {
                      color: rgb(53, 35, 77);
                    }
                  
                    & img {
                      width: 180px;
                    }
                  }

                }
              }
            }

          }
        }

        .swipe_me {
          position: absolute;
          top: 37%;
          right: 11%;
          color: #fff;
          text-align: center;
          font-size: 20px;

          & img {
            width: 100px;
            animation: animations 1.1s linear infinite;
          }
        }

        @keyframes animations {
          25% {
              transform: rotate(5deg) scale(1.01);
          }

          50% {
              transform: rotate(0deg) scale(1);
          }

          75% {
              transform: rotate(-5deg) scale(1.01);
          }
      }
      }

      .Con_r {
        width: 25%;
        height: 100%;
        // background-color: #8a8a8a62;
        background-image: url(../../public/img/sign.png);
        background-size: 60%;
        background-repeat: no-repeat;
        background-position: center;
        opacity: 0.5;
      }
    }

    .C_footer {
      width: 100%;
      height: 20%;

      .waves {
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 250px;
        margin-bottom: -7px;
        /*Fix for safari gap*/
        min-height: 180px;
        max-height: 250px;
      }

      .content {
        position: relative;
        height: 20vh;
        text-align: center;
        background-color: rgba(201, 173, 238, 0.705);
      }

      /* Animation */

      .parallax>use {
        animation: move-forever 25s cubic-bezier(.55, .5, .45, .5) infinite;
      }

      .parallax>use:nth-child(1) {
        animation-delay: -2s;
        animation-duration: 7s;
      }

      .parallax>use:nth-child(2) {
        animation-delay: -3s;
        animation-duration: 10s;
      }

      .parallax>use:nth-child(3) {
        animation-delay: -4s;
        animation-duration: 13s;
      }

      .parallax>use:nth-child(4) {
        animation-delay: -5s;
        animation-duration: 20s;
      }

      @keyframes move-forever {
        0% {
          transform: translate3d(-90px, 0, 0);
        }

        100% {
          transform: translate3d(85px, 0, 0);
        }
      }
    }

  }

}
