$bg: #fff;
$text: #202235;
$light-pink: #f7edfa;
$pink: #f7edfa;
$dark-pink: rgb(220, 193, 252);
$pink-border: rgb(191, 146, 218);
$pink-shadow: #4f395e;


#portfolio_wrap {
        width: 100%;
        height: 963px;
        position: relative;
        display: flex;
        align-items: center;


        .mySwiper {
                width: 90%;
                height: 90%;

                .swiper-pagination-bullets {
                        bottom: 8%;
                        left: -14.5%;

                        .swiper-pagination-bullet {
                                width: 120px;
                                height: 30px;
                                padding-top: 10px;
                                border-radius: 5px;
                                margin-right: 1%;
                                font-weight: 600;
                                // box-shadow: inset 2px 3px 10px 5px rgba(201, 210, 218, 0.815);
                                background: rgba(156, 156, 156, 0);
                                border: 2px solid #fff;
                                color: #632289;
                        }
                }

                .swiper-pagination-bullet-active {
                        background: rgba(0, 0, 0, 0);
                        width: 20px;
                        height: 20px;

                }

                .SwiperSlide {
                        width: 100%;
                        height: 100%;

                }
        }

        .tab_back {
                width: 100%;
                height: 100%;
                background-image: url(../../public/img/tab2.png);
                background-repeat: no-repeat;
                background-position: center;
                background-size: contain;


                .row {
                        width: 100%;
                        height: 100%;
                        display: flex;
                        align-items: center;
                        justify-content: center;

                        .col-xl-6 {
                                width: 60%;
                                height: 90%;
                                // background-color: rgba(117, 117, 117, 0.479);
                                display: flex;
                                align-items: center;
                                justify-content: flex-start;
                                position: relative;


                                h1 {
                                        position: absolute;
                                        top: -2.2%;
                                        left: 3%;
                                        color: #f8f8fa;
                                        font-size: 30px;
                                }

                                h3 {
                                        position: absolute;
                                        top: -1.5%;
                                        left: 30%;
                                        font-size: 22px;
                                        color: #4f4970;
                                }

                                h2 {
                                        position: absolute;
                                        top: 6.5%;
                                        left: 36%;
                                        color: #f4ecf2;
                                        font-size: 38px;
                                        color: white;
                                        text-shadow: 3px 3px 3px rgb(116, 116, 116);

                                        span {
                                                transform: scale(0.9);
                                                display: inline-block;
                                        }

                                        span:first-child {
                                                animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
                                        }

                                        span:last-child {
                                                animation: bopB 1.2s 0.2s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite alternate;
                                        }
                                }

                                @keyframes bop {
                                        0% {
                                                transform: scale(0.9);
                                        }

                                        50%,
                                        100% {
                                                transform: scale(1.01);
                                        }
                                }

                                @keyframes bopB {
                                        0% {
                                                transform: scale(0.9);
                                        }

                                        80%,
                                        100% {
                                                transform: scale(1.01) rotateZ(-3deg);
                                        }
                                }

                                .arrow1 {
                                        width: 65px;
                                        position: absolute;
                                        top: 8.5%;
                                        left: 29%;
                                        animation: animations 1.1s linear infinite;
                                }

                                .screen:before {
                                        content: "";
                                        display: block;

                                }

                                .screen {
                                        width: 100%;
                                        height: 57%;
                                        position: relative;
                                        display: flex;
                                        align-items: flex-start;
                                        justify-content: center;
                                        margin-top: -4%;
                                        cursor: pointer;

                                        .mac_top {
                                                width: 73%;
                                                position: relative;
                                                z-index: 5;
                                                margin-left: 4%;
                                                margin-top: -2%;
                                        }

                                        .mac_bottom {
                                                width: 33%;
                                                position: absolute;
                                                bottom: -22.5%;
                                                z-index: 5;
                                                right: 41.5%;
                                        }


                                        .viewport {
                                                position: absolute;
                                                bottom: 0;
                                                left: 3.5%;
                                                right: 0;
                                                top: -20%;
                                                margin: 10%;
                                                background-image: url(../../public/img/project_05.png);
                                                background-repeat: no-repeat;
                                                transition: background-position 8s ease;
                                                background-position: 0 0;
                                                background-size: 73.3% auto;
                                                cursor: pointer;
                                        }
                                }

                                .phone:before {
                                        content: "";
                                        display: block;

                                }

                                .phone {
                                        width: 100%;
                                        height: 57%;
                                        position: absolute;
                                        bottom: 10%;
                                        right: -28.5%;
                                        // overflow: hidden;
                                        display: flex;
                                        align-items: flex-start;
                                        justify-content: center;
                                        padding-top: 3.5%;
                                        // margin-top: %;
                                        cursor: pointer;

                                        & img {
                                                width: 105%;
                                        }

                                        a {
                                                z-index: 6;
                                        }

                                        .viewport2 {
                                                position: absolute;
                                                bottom: 0;
                                                left: 34.5%;
                                                right: 0;
                                                top: 18px;
                                                margin: 8.3%;
                                                background-image: url(../../public/img/project_05p.png);
                                                background-repeat: no-repeat;
                                                transition: background-position 10s ease;
                                                background-position: 0 0;
                                                background-size: 32.5% auto;
                                                cursor: pointer;
                                                z-index: 5;
                                        }
                                }

                        }

                        .screen:hover .viewport {
                                background-position: 0 100%;
                        }

                        .phone:hover .viewport2 {
                                background-position: 0 100%;
                        }

                        .col-xl-2 {
                                width: 30%;
                                height: 90%;
                                display: flex;
                                flex-direction: column;
                                align-items: center;
                                // background-color: rgba(173, 173, 173, 0.479);

                                .window_wrap {
                                        width: 80%;
                                        height: 40%;
                                        margin-top: 15%;
