                                                                text-align: center;
                                                                padding-top: 2px;
                                                                font-weight: bold;
                                                                color: rgb(153, 186, 214);
                                                        }
                                                }
                                        }

                                        .window {
                                                width: 100%;
                                                height: 88%;
                                                color: #fff;
                                                font-family: 'NanumSquareRound';
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                background-color: #8fbbe4c7;

                                                .text_box {
                                                        display: flex;
                                                        align-items: center;
                                                        flex-direction: column;


                                                        p {
                                                                width: 85%;
                                                                margin-bottom: 10px;
                                                        }

                                                        .notice {
                                                                line-height: 30px;
                                                                font-size: 17px;
                                                        }
                                                }

                                                .tags {
                                                        display: flex;
                                                        justify-content: center;

                                                        .tag_box {
                                                                position: relative;
                                                                margin: 0 2%;
                                                                font-family: 'NanumSquareRound';

                                                                & img {
                                                                        width: 110px;
                                                                }

                                                                p {
                                                                        position: absolute;
                                                                        top: 34%;
                                                                        left: 25%;
                                                                        transform: translate(0%, 20%);
                                                                        color: #171828;
                                                                        font-size: 14px;
                                                                }

                                                                .email2 {
                                                                        top: 34%;
                                                                        left: 28%;
                                                                }

                                                                .email3 {
                                                                        top: 19%;
                                                                        font-size: 13px;
                                                                        left: 17%;
