                                                        }
                                                }
                                        }
                                }

                                .window2_wrap {
                                        width: 80%;
                                        height: 40%;
                                        margin-top: 8%;
                                        border: 1px solid rgb(174, 192, 240);

                                        // background-color: rgba(134, 134, 134, 0.39);
                                        .window_top {
                                                width: 100%;
                                                height: 12%;
                                                background-color: #aec0f0;
                                                border-bottom: 1px solid rgb(149, 164, 204);

                                                p {
                                                        width: 20px;
