                                                .wbtn_top {
                                                        display: flex;
                                                        float: right;


                                                        .wbtn {
                                                                width: 25px;
                                                                height: 25px;
                                                                border: 2px solid rgb(148, 164, 209);
                                                                background: none;
                                                                margin: 5px;
                                                                text-align: center;
                                                                padding-top: 2px;
                                                                font-weight: bold;
                                                                color: rgb(148, 164, 209);
                                                        }
                                                }
                                        }

                                        .window {
                                                width: 100%;
