* {
    margin: 0;
    padding: 0;
}

:root {
    --primary-color: #914cec;
    --secondary-color: #f2f1fc;
}

$dark: #303030;
$gold: #a78247;
$borderwidth: 0.1vw;
$animationtime: 5s;

@mixin bordered() {
    border: $borderwidth solid $gold;
}

ul,
li,
ol {
    list-style: none;
}

a {
    text-decoration: none;
    color: #000;
}


@import url('https://fonts.googleapis.com/css2?family=Exo+2&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Black+Han+Sans&display=swap');


@font-face {
    font-family: 'RixInooAriDuriR';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2207-01@1.0/RixInooAriDuriR.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NanumSquareRound';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/NanumSquareRound.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'NanumBarunpen';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_two@1.0/NanumBarunpen.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}


.App {
    width: 100%;
    // height: 963px;
    font-family: 'Exo 2', sans-serif;

    #wrap {
        width: 100vw;
        height: 100vh;
        background: rgb(89, 68, 129);
        background: linear-gradient(0deg, rgba(89, 68, 129, 1) 0%, rgba(71, 49, 102, 1) 18%, rgba(62, 47, 94, 1) 37%, rgba(39, 41, 66, 1) 64%, rgba(23, 24, 40, 1) 100%);
        position: relative;
        // overflow: hidden;


        .swiper.AppSwiper {
            width: 100%;
            height: 100%;
            position: absolute;

            .swiper-pagination-bullet {
                background: rgba(156, 156, 156, 0.733);
            }

            .swiper-pagination-bullet-active {
                background: rgb(245, 250, 255);
                // width: 20px;
                // height: 20px;
            }

            .swiper-wrapper {
                width: 100%;
                height: 100%;

                .swiper-slide {
                    background: transparent;


                    .main_wrap {

                        width: 100%;
                        height: 100%;

                        .main_top {
                            width: 100%;
                            height: 65px;
                            position: relative;
                            z-index: 5;
                            // background-color: aquamarine;

                            .row {
                                height: 100%;
                                display: flex;
                                align-items: center;
                                justify-content: space-between;
                                // background-color: aliceblue;

                                .col-md-1 {
                                    color: rgb(244, 234, 252);
                                    font-size: 24px;
                                    font-weight: 600;

                                    p {
                                        width: 200px;
                                        margin-right: 5px;
                                    }

                                    .noti {
                                        width: 400px;
                                        font-size: 14px;
                                        margin-left: 10px;
                                    }

                                    h1 {
                                        width: 80%;
                                        max-width: 480px;
                                        margin-left: 5px;
                                        margin-top: 5px;
                                        font-family: 'Exo 2', sans-serif;
                                        font-size: 1.3em;
                                        line-height: 1.4em;
                                        font-weight: 700;
                                        color: #f3f3f3;
                                    }

                                    /* Underline */
                                    .underline {
                                        line-height: 0.5em;
                                        /* Positive value. */
                                        vertical-align: 0.5em;
                                        /* Positive value. */
                                        border-bottom: .3em solid transparent;
                                        -moz-border-image: -moz-linear-gradient(left, #A8DBED 0%, #E291E6 100%);
                                        -webkit-border-image: -webkit-linear-gradient(left, #A8DBED 0%, #E291E6 100%);
                                        border-image: linear-gradient(to right, #A8DBED 0%, #E291E6 100%);
                                        border-image-slice: 1;
                                    }

                                    .underline span {
                                        vertical-align: -0.2em;
                                    }

                                    /* Highlight */
                                    *::selection {
                                        background: rgba(226, 145, 230, .2);
                                    }

                                    & img {
                                        width: 50px;
                                        float: left;
                                        margin-right: 7px;
                                    }
                                }
                            }
                        }

                        .main_center {
                            width: 100%;
                            height: 800px;
                            position: relative;
                            z-index: 5;
                            // background-color: rgb(138, 138, 138, 0.5);

                            .row {
                                height: 100%;
                                display: flex;
                                align-items: center;
                                justify-content: space-between;

                                .col-md-1 {
                                    width: 10%;
                                    height: 100%;
                                    display: flex;
                                    align-items: center;


                                    // background-color: rgba(112, 112, 112, 0.7);
                                    .list {
                                        margin-left: 10px;
                                        margin-top: 30px;

                                        li {
                                            margin: 0 0 30px 10px;
                                            font-size: 40px;
                                            font-weight: bold;
                                            // font-family: 'Exo 2', sans-serif;
                                            font-family: 'RixInooAriDuriR';

                                            a {
                                                color: #e2e1f1;

                                                span {
                                                    cursor: pointer;
                                                    -webkit-text-stroke-color: #fff;
                                                    -webkit-text-stroke-width: 0.015em;
                                                    -webkit-text-fill-color: transparent;
                                                    -webkit-background-clip: text;
                                                    background-repeat: no-repeat;
                                                    transition: background-size .5s cubic-bezier(0.67, 0.01, 0.15, 0.98);
                                                }

                                                .top {
                                                    background-image: linear-gradient(180deg, #fff 0%, #fff 50%, transparent 50.1%);
                                                    background-position: 100% 0%;
                                                    background-size: 100% 0%;
                                                    text-shadow: 1px 4px 1px rgba(138, 138, 138, 0.678);

                                                    &:hover {
                                                        background-size: 100% 200%;
                                                    }
                                                }

                                                .right {
                                                    background-image: linear-gradient(270deg, #fff 0%, #fff 50%, transparent 50.1%);
                                                    background-position: 100% 0%;
                                                    background-size: 0% 100%;
                                                    text-shadow: 1px 4px 1px rgba(138, 138, 138, 0.678);

                                                    &:hover {
                                                        background-size: 200% 100%;
                                                    }
                                                }

                                                .angle {
                                                    background-image: linear-gradient(135deg, #fff 0%, #fff 50%, transparent 50.1%);
                                                    background-size: 0% 100%;
                                                    text-shadow: 1px 4px 1px rgba(138, 138, 138, 0.678);

                                                    &:hover {
                                                        background-size: 220% 100%;
                                                    }
                                                }

                                                .radial {
                                                    background-image: radial-gradient(circle farthest-corner at center center, #fff 0%, #fff 50%, transparent 50.1%);
                                                    background-position: 50% 50%;
                                                    background-size: 0% 0%;
                                                    text-shadow: 1px 4px 1px rgba(138, 138, 138, 0.678);

                                                    &:hover {
                                                        background-size: 180% 400%; // Depends by text size
                                                    }
                                                }
                                            }
                                        }
                                    }
                                }

                                .col-md-8 {
                                    height: 100%;
                                    display: flex;
                                    align-items: flex-end;
                                    // background-color: rgba(163, 163, 163, 0.7);

                                    .main_box {
                                        width: 880px;
                                        height: 730px;
                                        display: flex;
                                        align-items: center;
                                        justify-content: center;
                                        position: relative;

                                        .window_box {
                                            .window_wrap {
                                                position: relative;

                                                .moon {
                                                    position: absolute;
                                                    top: 57%;
                                                    right: -14%;

                                                    & img {
                                                        width: 100px;
                                                        position: absolute;
                                                        z-index: 6;
                                                    }
                                                }

                                                .arrow8 {
                                                    position: absolute;
                                                    top: 12%;
                                                    left: -122%;
                                                    z-index: 5;

                                                    & img {
                                                        width: 70px;
                                                        animation: animations 1.3s linear infinite;
                                                    }
                                                }

                                                .like {
                                                    position: absolute;
                                                    bottom: -8%;
                                                    left: -78%;

                                                    & img {
                                                        width: 60px;
                                                        transform: rotate(-10deg);
                                                    }
                                                }

                                                .like img {
                                                    // opacity: 0.9;
                                                    cursor: pointer;
                                                    animation: animation2 1.3s linear infinite;
                                                }

                                                @keyframes animation2 {
                                                    25% {
                                                        transform: rotate(5deg) scale(1.1);
                                                    }

                                                    50% {
                                                        transform: rotate(0deg) scale(1);
                                                    }

                                                    75% {
                                                        transform: rotate(-20deg) scale(1.1);
                                                    }
                                                }

                                                .email {
                                                    position: absolute;
                                                    top: 16%;
                                                    left: -110%;

                                                    & img {
                                                        width: 160px;
                                                        cursor: pointer;
                                                    }

                                                    & img:hover {
                                                        opacity: 0.9;
                                                        transform: scale(1.02);
                                                    }
                                                }

                                                .star2 {
                                                    position: absolute;
                                                    top: -6%;
                                                    right: 32%;

                                                    & img {
                                                        width: 50px;
                                                    }
                                                }

                                                .illustration-editor {
                                                    margin-top: -10rem;
                                                    margin-right: -10rem;
                                                    display: grid;
                                                    grid-template-rows: 24px auto;
                                                    justify-items: center;
                                                    position: relative;
                                                    z-index: 6;
                                                }

                                                .code {
                                                    position: absolute;
                                                    width: 290px;
                                                    height: 145px;
                                                    background: #4a4c554f;
                                                    box-shadow: 0px 4px 30px rgba(0, 0, 0, 0.5);
                                                    border-radius: 6px;
                                                    z-index: 5;

                                                    header {
                                                        display: grid;
                                                        grid-template-columns: 36px auto 36px;
                                                        align-items: center;

                                                        svg {
                                                            margin: 10px;
                                                        }

                                                        h1 {
                                                            font-family: Lato, sans-serif;
                                                            font-weight: 900;
                                                            font-size: 14px;
                                                            letter-spacing: 1.57px;
                                                            color: #C5C8D4;
                                                            margin: 0;
                                                        }
                                                    }

                                                    &-html {
                                                        left: -80px;
                                                        top: 40px;

                                                    }

                                                    &-css {
                                                        left: -140px;
                                                        top: 200px;
                                                        height: 160px;
                                                        margin-top: 5px;
                                                    }

                                                    &-js {
                                                        left: -35px;
                                                        top: 360px;
                                                        margin-top: 25px;
                                                        height: 160px;
                                                    }

                                                    .code-content {
                                                        margin: 0 10px 10px;
                                                        color: white;

                                                        .c-r {
                                                            color: #A88038
                                                        }

                                                        .c-o {
                                                            color: #DE7300
                                                        }

                                                        .c-y {
                                                            color: #E1CA72
                                                        }

                                                        .c-g {
                                                            color: #74B087
                                                        }

                                                        .c-p {
                                                            color: #9F8198
                                                        }

                                                        .c-b {
                                                            color: #7A99AD
                                                        }

                                                        .c-c {
                                                            color: #666666
                                                        }
                                                    }

                                                    &-css {
                                                        .code-content code {
                                                            .line {
                                                                @for $i from 1 through 5 {
                                                                    &-#{$i} {
                                                                        width: 0%;
                                                                        margin: 0;
                                                                        border-right: .15em solid transparent;
                                                                        overflow: hidden;
                                                                        display: inline-block;
                                                                        white-space: nowrap;
                                                                    }
                                                                }

                                                                @for $i from 1 through 4 {
                                                                    &-#{$i} {
                                                                        animation: typing-#{$i} 3s unquote((($i - 1) * 3) + 's') steps(30, end) forwards,
                                                                        cursor 1s linear unquote((($i - 1) * 3) + 's') 3;
                                                                    }
                                                                }

                                                                &-1,
                                                                &-3,
                                                                &-4 {
                                                                    animation-duration: 1s;
                                                                    animation-timing-function: steps(10, end);
                                                                }

                                                                &-5 {
                                                                    border-color: transparent;
                                                                    animation:
                                                                        typing-5 3s 12s steps(30, end) forwards,
                                                                        cursor-persist 1s linear 12s infinite;
                                                                }

                                                                &-2 {
                                                                    margin-left: 2rem;
                                                                }

                                                                &-3,
                                                                &-4,
                                                                &-5 {
                                                                    margin-left: 4rem;
                                                                }
                                                            }
                                                        }
                                                    }
                                                }

                                                .editor-block {
                                                    position: relative;
                                                    display: block;
                                                    width: auto;
                                                    height: 421px;
                                                    // background: linear-gradient(109.61deg, #4C4F5A 4.26%, #202125 84.84%);
                                                    border-radius: 10px;
                                                }

                                                @keyframes typing-1 {
                                                    from {
                                                        width: 0
                                                    }

                                                    to {
                                                        width: 60px
                                                    }
                                                }

                                                @keyframes typing-2 {
                                                    from {
                                                        width: 0
                                                    }

                                                    to {
                                                        width: 220px
                                                    }
                                                }

                                                @keyframes typing-3 {
                                                    from {
                                                        width: 0
                                                    }

                                                    to {
                                                        width: 70px
                                                    }
                                                }

                                                @keyframes typing-4 {
                                                    from {
                                                        width: 0
                                                    }

                                                    to {
                                                        width: 75px
                                                    }
                                                }

                                                @keyframes typing-5 {
                                                    from {
                                                        width: 0
                                                    }

                                                    to {
                                                        width: 141px
                                                    }
                                                }


                                                @keyframes cursor {

                                                    from,
                                                    to,
                                                    20%,
                                                    80% {
                                                        border-color: transparent
                                                    }

                                                    25%,
                                                    75% {
                                                        border-color: white
                                                    }
                                                }

                                                @keyframes cursor-persist {

                                                    from,
                                                    45% {
                                                        border-color: transparent
                                                    }

                                                    50%,
                                                    to {
                                                        border-color: white
                                                    }
                                                }
                                            }
                                        }

                                        .laptop_box {
                                            z-index: 6;
                                            position: absolute;
                                            right: 8%;
                                            bottom: 17%;

                                            .laptop_wrap {
                                                position: relative;

                                                .arrow2 {
                                                    position: absolute;
                                                    top: -67%;
                                                    left: 48%;
                                                    z-index: 5;

                                                    & img {
                                                        width: 70px;
                                                        animation: animations 1.3s linear infinite;
                                                    }
                                                }

                                                .cloud {
                                                    position: absolute;
                                                    top: -72%;
                                                    left: -17%;

                                                    & img {
                                                        width: 260px;
                                                    }
                                                }

                                                .cloud img:hover {
                                                    opacity: 0.9;
                                                    cursor: pointer;
                                                    transform: scale(1.02);
                                                }

                                                .wifi {
                                                    position: absolute;
                                                    top: -19%;
                                                    left: 9%;

                                                    & img {
                                                        width: 100px;
                                                    }
                                                }

                                                .laptop {
                                                    & img {
                                                        width: 340px;
                                                        cursor: pointer;
                                                    }
                                                }

                                                .laptop img:hover {
                                                    opacity: 0.9;
                                                    transform: scale(1.02);
                                                }

                                                @keyframes animations {
                                                    25% {
                                                        transform: rotate(5deg) scale(1.01);
                                                    }

                                                    50% {
                                                        transform: rotate(0deg) scale(1);
                                                    }

                                                    75% {
                                                        transform: rotate(-5deg) scale(1.01);
                                                    }
                                                }

                                                .arrow7 {
                                                    position: absolute;
                                                    bottom: -2%;
                                                    right: 8%;

                                                    & img {
                                                        width: 65px;
                                                        animation: animations 1s linear infinite;
                                                    }
                                                }

                                                .thumbs {
                                                    position: absolute;
                                                    bottom: 42%;
                                                    right: 1%;

                                                    & img {
                                                        width: 70px;
                                                        transform: rotate(7deg);
                                                    }
                                                }

                                                .star {
                                                    position: absolute;
                                                    top: -24%;
                                                    right: 15%;

                                                    & img {
                                                        width: 60px;
                                                        transform: rotate(-15deg);
                                                    }
                                                }

                                                .folder {
                                                    position: absolute;
                                                    bottom: -20%;
                                                    left: -10%;

                                                    & img {
                                                        width: 140px;
                                                        transform: rotate(10deg);
                                                    }
                                                }
                                            }

                                        }

                                        .moon_wrap {
                                            width: 28vw;
                                            height: 28vw;
                                            position: relative;

                                            .moon {
                                                width: 28vw;
                                                background: rgb(104, 100, 180);
                                                background: linear-gradient(0deg, rgba(104, 100, 180, 1) 0%, rgba(103, 77, 145, 1) 8%, rgba(77, 47, 111, 1) 21%, rgba(45, 33, 91, 1) 37%, rgba(40, 25, 78, 1) 50%, rgba(42, 17, 54, 1) 68%, rgba(28, 28, 28, 1) 90%);
                                                height: 28vw;
                                                display: flex;
                                                align-items: center;
                                                justify-content: center;
                                                border-radius: 50%;
                                                font-size: 2rem;
                                                color: white;
                                                font-weight: bold;
                                                position: relative;
                                                z-index: 5;

                                            }

                                            &::before,
                                            &::after {
                                                position: absolute;
                                                content: "";
                                                top: 17%;
                                                left: 15%;
                                                height: 67%;
                                                width: 67%;
                                                border-radius: 50%;
                                                opacity: 0.4;
                                                filter: blur(70px);
                                            }

                                            &::after {
                                                background-color: #3462fc;
                                                z-index: 2;
                                                animation: backglow2 15s ease infinite;
                                            }

                                            &::before {
                                                background-color: #ee54d4;
                                                z-index: 1;
                                                animation: backglow1 15s ease infinite;
                                            }
                                        }

                                    }
                                }

                                @keyframes backglow1 {
                                    0% {
                                        transform: translate(10%, 10%) scale(1);
                                        opacity: 0.5;
                                    }

                                    25% {
                                        transform: translate(-10%, 10%) scale(0.9);
                                        opacity: 0.5;
                                    }

                                    50% {
                                        transform: translate(-10%, -10%) scale(1.2);
                                        opacity: 0.8;
                                    }

                                    75% {
                                        transform: translate(10%, -10%) scale(1);
                                        opacity: 0.2;
                                    }

                                    100% {
                                        transform: translate(10%, 10%) scale(1);
                                        opacity: 0.5;
                                    }
                                }

                                @keyframes backglow2 {
                                    0% {
                                        transform: translate(-10%, -10%) scale(1);
                                        opacity: 0.5;
                                    }

                                    25% {
                                        transform: translate(10%, -10%) scale(0.9);
                                        opacity: 0.5;
                                    }

                                    50% {
                                        transform: translate(10%, 10%) scale(1.2);
                                        opacity: 0.8;
                                    }

                                    75% {
                                        transform: translate(-10%, 10%) scale(1);
                                        opacity: 0.2;
                                    }

                                    100% {
                                        transform: translate(-10%, -10%) scale(1);
                                        opacity: 0.5;
                                    }
                                }

                                .my_tag {
                                    width: 10%;
                                    height: 100%;
                                    position: relative;
                                    // background-color: rgba(163, 163, 163, 0.7);

                                    .tag_div {
                                        width: 40px;
                                        height: 150px;
                                        background-color: #ebeaf5;
                                        border-radius: 5px;
                                        text-align: center;
                                        position: fixed;
                                        bottom: 12%;
                                        right: 0;

                                        .tag_point {
                                            height: 30px;
                                            background-color: #bbb9da;
                                            border-radius: 5px;
                                        }

                                        .top_btn {
                                            position: absolute;
                                            top: -9px;
                                            left: -12px;
                                        }
                                    }

                                    .tag_div {
                                        cursor: pointer;
                                    }

                                }
                            }
                        }

                        .main_footer {
                            width: 100%;
                            height: 95px;
                            position: relative;
                            z-index: 1;
                            display: flex;
                            align-items: flex-start;
                            justify-content: center;
                            // background-color: #4a4c554f;

                            .row {

                                .col-md-4 {

                                    .bar {
                                        width: 1000px;
                                        height: 50px;
                                        border-radius: 10PX;
                                        border-radius: 99px;
                                        background-color: #c5c5d8d3;


                                        .tabs {
                                            display: flex;
                                            justify-content: space-around;
                                            position: relative;
                                            // background-color: #fff;
                                            box-shadow: 0 0 1px 0 rgba(#185ee0, 0.15), 0 6px 12px 0 rgba(#185ee0, 0.15);
                                            border-radius: 99px; // just a high number to create pill effect

                                            * {
                                                z-index: 2;
                                            }
                                        }

                                        input[type="radio"] {
                                            display: none;
                                        }

                                        .tab {
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;
                                            height: 50px;
                                            width: 170px;
                                            font-size: 1rem;
                                            font-weight: 500;
                                            font-family: 'Comfortaa', sans-serif;
                                            border-radius: 99px; // just a high number to create pill effect
                                            cursor: pointer;
                                            transition: color 0.15s ease-in;
                                        }

                                        .notification {
                                            display: flex;
                                            align-items: center;
                                            justify-content: center;
                                            width: 2rem;
                                            height: 2rem;
                                            margin-left: 0.75rem;
                                            border-radius: 50%;
                                            background-color: var(--secondary-color);
                                            transition: 0.15s ease-in;
                                        }

                                        input[type="radio"] {
                                            &:checked {
                                                &+label {
                                                    color: var(--primary-color);

                                                    &>.notification {
                                                        background-color: var(--primary-color);
                                                        color: #fff;
                                                    }
                                                }
                                            }
                                        }

                                        input[id="radio-1"] {
                                            &:checked {
                                                &~.glider {
                                                    transform: translateX(-235%);
                                                }
                                            }
                                        }

                                        input[id="radio-2"] {
                                            &:checked {
                                                &~.glider {
                                                    transform: translateX(-120%);
                                                }
                                            }
                                        }

                                        input[id="radio-3"] {
                                            &:checked {
                                                &~.glider {
                                                    transform: translateX(0%);
                                                }
                                            }
                                        }

                                        input[id="radio-4"] {
                                            &:checked {
                                                &~.glider {
                                                    transform: translateX(118%);
                                                }
                                            }
                                        }

                                        input[id="radio-5"] {
                                            &:checked {
                                                &~.glider {
                                                    transform: translateX(235%);
                                                }
                                            }
                                        }

                                        .glider {
                                            position: absolute;
                                            display: flex;
                                            height: 50px;
                                            width: 170px;
                                            background-color: var(--secondary-color);
                                            z-index: 1;
                                            border-radius: 99px; // just a high number to create pill effect
                                            transition: 0.25s ease-out;
                                        }

                                        @media (max-width: 700px) {
                                            .tabs {
                                                transform: scale(0.6);
                                            }
                                        }
                                    }

                                }
                            }
                        }
                    }


                }

            }

        }



    }

}
